import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProfileService } from '../profile.service';

@Component({
  selector: 'app-title-edit',
  templateUrl: './title-edit.component.html',
  styleUrls: ['./title-edit.component.scss']
})
export class TitleEditComponent implements OnInit {

  public username: String;

  constructor(private route: ActivatedRoute, private user: ProfileService, private router: Router) {
    this.username = this.route.snapshot.params['username'];
   }

  ngOnInit() {
  }

  UpdateTitle(event){
   
    const Status = event.target.querySelector('#Status').value;
    const Description = event.target.querySelector('#Description').value;
    

    this.user.EditTitle(this.username,Status,Description).subscribe(data => {
     
     if(data != null){
       alert('Status Added');
       this.router.navigate(['/profile',this.username]);
      }
    })
  }
  HomePage(){
    return this.router.navigate(['profile',this.username])
  }
}
